.App {
    text-align: center;
    /* padding: 10px; */
}

.App-logo {
    /* height: 40vmin; */
    pointer-events: none;
    transform-origin: center;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    /* background-color: #282c34; */
    background-color: #fff;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.bluetooth__connect-btn {
    background: transparent;
    border: none !important;
    outline: none !important;
}

.login-btn-wrap {
    display: flex;
    margin-top: 25px;
    .google-oauth-btn {
        margin-right: 10px;
    }

    .kep-login-facebook {
        text-transform: capitalize;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 2px 0px,
            rgba(0, 0, 0, 0.24) 0px 0px 1px 0px;
        border-radius: 2px;
        // background-color: #fff;
        font-size: 14px;
        font-weight: 500;
        font-family: Roboto, sans-serif;
        display: inline-flex;
        padding-left: 12px;
        padding-right: 12px;
        border: 1px solid transparent;
        &::before {
            content: '';
            display: inline-block;
            background: url('assets/images/facebook.svg');
            width: 20px;
            height: 20px;
            margin-right: 14px;
        }
    }
}

.basicBox {
    text-transform: uppercase;
    width: 130px;
    height: 65px;
    text-align: center;
    line-height: 65px;
    margin: 10px auto;
    position: relative;
    cursor: pointer;
    color: #fff;
}
